import React from 'react';
import FlowComponent from '../common/component/core/FlowComponent';
import $, { event } from 'jquery';
import {Form, Button } from 'react-bootstrap';
import '../../styles/flow.css';
import {kycProcess, kycApplication} from '../../actions/kyc_actions';

class KYCProcess extends FlowComponent {
    state = {
        rule_groups: [{}], 
        
    }
    
    componentWillMount(){
        
        const cust_id = this.props.match.params.cust_id;

        kycApplication(this.req({cust_id}))
         .then((response) => {
          	const result = response.data
                console.log(response.data);
              this.setState({
                rule_groups:result, cust_id
               }); 
             
           });
            // this.setState({cust_id,rule_groups});
            // var jp = require('jsonpath');
            // var names = jp.query(this.state.rule_groups, "$..rules[?(@.rule_id=='5')]");
    }
    KycProcess = (event) =>{ 

       kycProcess(this.req({"kyc_rules":this.state.rule_groups,"cust_id":this.state.cust_id}))
       .then((response) => {
            if(!response){return };
            if(response){
                alert("KYC data updated successfully");
                window.location.href = "/borrower/indiv/view/"+this.state.cust_id;
            }
       }
       );
                
    }
    kycOnChange = (event) => {
        var jp = require('jsonpath');
        var rule_groups = this.state.rule_groups
        const jsonpath = `$..rules[?(@.rule_id=='${event.target.id}')].rule_status`
        
        
        var status = jp.value(rule_groups, jsonpath);
       
        if(status == 'on'){
            status = 'off'
        }else{
            status = 'on'
        }

        jp.value(rule_groups, jsonpath, status);

        
        this.setState({rule_groups});

    }
    render(){
        let status;
       
        return(
            <div className="kyc-container">
                <div className="kyc_heading"><h1><b>KYC Process for {this.state.cust_id}</b></h1></div>
                {this.state.rule_groups.map((rule_group,i)=>
                    <>
                        <div className="kyc_rule"><b>{rule_group.group_name}</b></div>
                        <ul>  
                            {rule_group.rules && rule_group.rules.map((rule,i) =>
                            <div className="kyc_rule" style={{padding:'10px'}}  >
                            {rule.rule_status == "on" ? status = true : status =false}
                            <li style={{display:'inline'}}>{rule.rule} 
                            
                            <Form.Check className="toggle_switch" type="switch"  style={{display:'inline',float:"right"}} id={rule.rule_id} label="" onChange={this.kycOnChange} checked ={status}/><br/>
                            </li> 
                            
                        </div>
                            )}
                            
                        </ul>
            
                    </>
                
                    )
                }
                <Button type='submit' onClick={this.KycProcess}>Submit</Button>
            </div>
        
        );
    }
}
export default KYCProcess;