import React from 'react';
import FlowContainer from '../../common/container/core/FlowContainer';
import $ from 'jquery';	
import SearchFieldVisit from '../component/SearchFieldVisit';
import {getFieldVisitData} from '../../../actions/field_visit_actions';
import {format_date_tbl} from '../../../helpers/common_helper';
import {dd_value} from '../../../actions/common_actions';



class FieldVisitContainer extends FlowContainer{

	state = {
		field_visit : {},
		visit_data : "hide_div",
		visit_recds :null
	}


	validate_search(field_visit) {
		 if(field_visit.visit_start_time__from && field_visit.visit_start_time__to && field_visit.rel_mgr_id){
			return true
  	}else{
  		alert("Please enter valid search criteria")
  		return false}
  	
	}

	handleSearch = (event) => {
		const request = this.state.field_visit
		var is_valid = this.validate_search(request)
		if(is_valid){
			getFieldVisitData(this.req(request))
			    .then((response) => {
			         var prev_table = $('#field_visits').DataTable();
               	 		prev_table.destroy();
			        	if(!response){return };
	        				if(response.status === "success"){        					
	        					const visit_recds = response.data.visit_recd.map((record,i)=>
        						<tr >
        							<td>{record.cust_id}</td>
        							<td>{record.cust_name}</td>
        							<td>{format_date_tbl(record.visit_start_time,true)}</td>
        							<td>{dd_value(record.visit_purpose)}</td>
        							<td>{record.remarks}</td>
        						</tr>
	        						);
	        		this.setState({visit_recds,visit_data:"show_div"});
             		$('#field_visits').removeAttr('width').dataTable({
                  		"pageLength": 50,
		                "order" : [],	                
		                    "columns": [
		                      {"width" : "5%"},
		                      {"width" : "5%"},
		                      {"width" : "5%"},
		                      {"width" : "5%"},
		                      {"width" : "5%"}
		                    ],
		                    dom:'Bfrtip',
		                    buttons: [
		                    'csv','excel','print'
		                    ]
		            });        
	        				};
	      			})
		
		}

	 }

	 handlevisitchange =(event) =>{
	 	var field_visit = this.state.field_visit
  		field_visit[event.target.id] = event.target.value  	   
	 }


render(){
    return(
    	<div className="container">
	    	<div className="row">
	    		<h3 className="field_visit">Search Customer Visits</h3>
	    	</div>
	    	<SearchFieldVisit id="searchfieldvisit"  
					onSearch={this.handleSearch} 
					onComponentChange={this.handlevisitchange} 
					onComponentReset={this.handleComponentReset} 
					onComponentElementChange={this.handlevisitchange}/>
			<div className={this.state.visit_data}>
            	<div id='datatable '>  	    			
                	<table id ="field_visits" className='acc-stmt-table display strip row-border order-column list'>
      	             	<thead>
	                        <tr> 
	                            <th>Cust ID</th>
	                            <th>Cust Name</th>	                             
	                            <th>Visit Time</th>
	                            <th>Purpose</th>
	                            <th>Remarks</th>
                        	</tr>
                    	</thead>
                    	<tbody> 
                        	{this.state.visit_recds}
                    	</tbody>
                	</table>          
            	</div>
    		</div>
    	</div>
    	)
}
}


export default FieldVisitContainer;