import React from 'react';
import FlowComponent from '../../common/component/core/FlowComponent';
import {Form, Col , Button } from 'react-bootstrap';
import DatePicker from 'react-datetime';
import moment from 'moment';


class SearchFieldVisit extends FlowComponent{

state = {
		start_date :null
	}
	

componentWillMount(){
	var associated_with =  "flow";
	var associated_entity_code =  this.data_prvdr_code;
    this.setOptionsToStateFromApi("rel_mgr", {...this.def_req, associated_with, associated_entity_code});	
	}
	
	start_date = (event) =>{
		const value = moment(event).format("YYYY-MM-DD");
		this.setState({start_date : value});
		}	


render(){
	return(
		<Form >
	        <Form.Row>
	            <Form.Group as={Col} className="col-3">
	             	<Form.Label>Flow Visit</Form.Label><font className="text-danger">   *  </font>
			      	<Form.Control as="select" id="rel_mgr_id" onChange={(e) =>{this.props.onComponentElementChange(e)}}>
			      		{this.state.flow_rel_mgr_dd}
	                </Form.Control>
		       	</Form.Group>
			    <Form.Group as={Col} className="col-2">
			    	<Form.Label>From </Form.Label><font className="text-danger">   *  </font>
			    	<DatePicker  closeOnSelect={true}  dateFormat="DD MMM YYYY" timeFormat={false} required="required" onChange={(value) => {this.handleDateChange("visit_start_time__from", value);this.start_date(value)}} />
			    </Form.Group>
			    <Form.Group as={Col} className="col-2">
			    	<Form.Label>To </Form.Label><font className="text-danger">   *  </font>
			    	<DatePicker      closeOnSelect={true}   dateFormat="DD MMM YYYY" timeFormat={false} required="required" onChange={(value) => {this.handleDateChange("visit_start_time__to", value)}} />
			    </Form.Group>
			    <Form.Group as={Col} className="col-3">
			    	<Form.Label>Purpose</Form.Label>
			      	<Form.Control as="select" id="visit_purpose" onChange={(e) =>{this.props.onComponentElementChange(e)}}>
			      		{this.getSelectOptions("visit_purpose")}
	                </Form.Control>
		       	</Form.Group>
	        </Form.Row>
	        <Form.Row className="justify-content-md-center">
	        	<button type="button" className="btn btn-primary " id="search" onClick={this.props.onSearch} >Search</button>
	        </Form.Row>
	    </Form >)
	}
}

export default SearchFieldVisit;